<script setup lang="ts">
import { onBeforeMount } from "vue";
import { storeToRefs } from "pinia";
import { ACol, ASelect, ACheckbox } from "@actindo/core-ui";
import type { IDataTypeInstance } from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import { useDataTypesStore } from "@/stores/dataTypes";
import { IDataTypesSimplePricingValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesSimplePricingValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesSimplePricingValueConfiguration>();

const dataTypesStore = useDataTypesStore();
const { currencies, isCurrenciesLoading } = storeToRefs(dataTypesStore);

onBeforeMount(dataTypesStore.fetchCurrencies);
</script>

<template>
  <ACol>
    <ACheckbox
      v-model="configuration.isGross"
      :label="$t('simplePricing.isGross')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.msrp"
      :label="$t('simplePricing.msrp')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.promotions"
      :label="$t('simplePricing.promotions')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ASelect
      v-model="configuration.currencyCode"
      :items="currencies"
      :loading="isCurrenciesLoading"
      :label="$t('simplePricing.currency')"
      :readonly="readonly"
      item-title="name"
      item-value="id"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>
